import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function netIncomePerShareTtm(tableName: string) {
  const title = "Earnings/Share (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["netIncomePerShareTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Net profit per share.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about earnings per share?`,
              value: info.row.original.financialData.companyRatios?.data?.netIncomePerShareTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.netIncomePerShareTtm}
        />
      )
    },
  })
}

export { netIncomePerShareTtm }
